.loader-bg{
    position: absolute;
    width: 100%;
    height: 100vh;
    background-color: #EBADAD;
    z-index: 99;
    animation: fade-out 1s ease forwards 6s;
}

.loader-text{
    position: absolute;
    top:50%;
    left: 50%;
    transform:translate(-50%,-50%);
    animation: fill .8s ease forwards 4s;
}

.loader-text path:nth-child(1){
    stroke-dasharray: 273;
    stroke-dashoffset: 273;
    animation: line-anim 2s ease forwards;
}
.loader-text path:nth-child(2){
    stroke-dasharray: 279;
    stroke-dashoffset: 279;
    animation: line-anim 2s ease forwards .3s;
}
.loader-text path:nth-child(3){
    stroke-dasharray: 175;
    stroke-dashoffset: 175;
    animation: line-anim 2s ease forwards .7s;
}
.loader-text path:nth-child(4){
    stroke-dasharray: 175;
    stroke-dashoffset: 175;
    animation: line-anim 2s ease forwards 1s;
}
.loader-text path:nth-child(5){
    stroke-dasharray: 270;
    stroke-dashoffset: 270;
    animation: line-anim 2s ease forwards 1.2s;
}
.loader-text path:nth-child(6){
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: line-anim 2s ease forwards 1.5s;
}
.loader-text path:nth-child(7){
    stroke-dasharray: 174;
    stroke-dashoffset: 174;
    animation: line-anim 2s ease forwards 1.7s;
}
.loader-text path:nth-child(8){
    stroke-dasharray: 284;
    stroke-dashoffset: 284;
    animation: line-anim 2s ease forwards 2s;
}
.loader-text path:nth-child(9){
    stroke-dasharray: 274;
    stroke-dashoffset: 274;
    animation: line-anim 2s ease forwards 2.1s;
}
.loader-text path:nth-child(10){
    stroke-dasharray: 274;
    stroke-dashoffset: 274;
    animation: line-anim 2s ease forwards 2.3s;
}

@media screen and (max-width:960px){
    .loader-text{
        width: 80% !important;
    }
}

@keyframes line-anim {
    to{
        stroke-dashoffset: 0;
    }
}

@keyframes fill {
    from {
        fill:transparent;
    }
    to{
        fill: rgb(241, 203, 203);
    }
}

@keyframes fade-out {
    to{
        opacity: 0;
        display: none;
    }
}